@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  background-color: #5e5e5e;
  background-size: cover;
  font-family: "Montserrat", sans-serif; }

.logo {
  display: block;
  height: 15%;
  width: 15%;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto; }

.navbar {
  width: 550px;
  margin: 0px auto;
  text-align: center;
  font-size: 25px; }
  .navbar a {
    color: #94dde3; }

.leaderboard {
  width: 550px;
  margin: 0px auto;
  text-align: center; }

.leadheader {
  width: 100%;
  height: 70px;
  background: #94dde3;
  text-align: center;
  border-radius: 5px 5px 0 0; }
  .leadheader h2 {
    margin: 0;
    color: #fefefe;
    padding-top: 20px; }

.colheader {
  background: rgba(254, 254, 254, 0.9);
  width: 100%;
  margin-left: 0;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .colheader h4 {
    font-size: 15px; }
  .colheader .alltime {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .colheader .recent {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }

.users {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  background: #fefefe;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 5px;
  text-align: center; }
  .users .name {
    text-align: left; }
    .users .name a {
      padding-top: 15px;
      vertical-align: middle; }
  .users img {
    float: left;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid #94dde3;
    margin-right: 10px; }
  .users .rank {
    padding-left: 6px; }
  .users:nth-of-type(odd) {
    background: rgba(254, 254, 254, 0.9); }
  .users:nth-of-type(even) {
    background: rgba(240, 240, 240, 0.9); }
  .users:last-child {
    border-radius: 0 0 5px 5px; }

@media (max-width: 550px) {
  .users .name {
    padding-left: 5px; }
  .container {
    width: 100%; } }

@media (max-width: 540px) {
  .container {
    padding: 0; }
  .colheader div[class^='col-xs'] {
    padding: 0; }
  .users {
    text-align: center; }
    .users img {
      display: block;
      float: none;
      margin: 0 auto;
      width: 35px;
      height: 35px; }
    .users .name {
      text-align: center; }
      .users .name a {
        display: block;
        padding-top: 4px; } }

